import React from "react"
import { useStaticQuery, graphql } from "gatsby"
import Img from "gatsby-image"
import styled from "styled-components"
import PropTypes from "prop-types"

const UserWrapper = styled.div`
  display: flex;
  align-items: center;
  margin: 0 auto 12px auto;
  &:last-child {
    margin-bottom: 0;
  }
`

const Description = styled.div`
  flex: 1;
  margin-left: 18px;
  padding: 12px;
`
const Username = styled.h3`
  margin: 0 0 12px 0;
  padding: 0;
`

const Nickname = styled.span`
  font-size: 0.9rem;
  font-weight: bold;
  color: #999;
`

const Excerpt = styled.p`
  margin: 0;
`

const User = props => {
  const data = useStaticQuery(graphql`
    query {
      profileImage: file(relativePath: { eq: "haru-icon.png" }) {
        childImageSharp {
          fixed(width: 100, height: 100) {
            ...GatsbyImageSharpFixed
          }
        }
      }
    }
  `)

  return (
    <UserWrapper>
      <Img fixed={data.profileImage.childImageSharp.fixed} alt="haru-thumbs-up" />
      <Description>
        <Username>
          {props.username} <Nickname>a.k.a {props.nickname}</Nickname>
        </Username>
        <Excerpt>{props.excerpt}</Excerpt>
      </Description>
    </UserWrapper>
  )
}

User.propTypes = {
  username: PropTypes.string,
  excerpt: PropTypes.string,
}

User.defaultProps = {
  username: ``,
  excerpt: ``,
}

export default User
